import React, { Component } from "react";
import { strings } from "../../initialState";
import { topBairros } from "../../bairros";
import InputRange from "react-input-range";
import StickyBox from "react-sticky-box";
import "react-input-range/lib/css/index.css";

// Font Awesome
import { library } from "@fortawesome/fontawesome-svg-core";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { fas } from "@fortawesome/free-solid-svg-icons";
import { fab } from "@fortawesome/free-brands-svg-icons";
library.add(fab, fas);

class BuscaFormulario extends Component {
  state = {
    area: { min: 0, max: 100 },
    preco: { min: 0, max: 100 },
    finalidade: "Comprar",
    showBairros: false
  };
  _handleFinalidadeChange = finalidade => {
    this.setState({ finalidade: finalidade });
  };
  _handleShowBairros = () => {
    const showBairros = !this.state.showBairros;
    this.setState({ showBairros });
  };
  render() {
    const { imovelProp } = strings;
    const { lang } = this.props;
    return (
      <StickyBox offsetTop={15} offsetBottom={15}>
        <div className="buscaForm">
          <div className="row">
            <div
              className={
                this.state.finalidade === "Comprar"
                  ? "col-6 buscaForm__finalidade active"
                  : "col-6 buscaForm__finalidade"
              }
              onClick={() => this._handleFinalidadeChange("Comprar")}
            >
              <span>{imovelProp[lang].comprar}</span>
            </div>
            <div
              className={
                this.state.finalidade === "Alugar"
                  ? "col-6 buscaForm__finalidade active"
                  : "col-6 buscaForm__finalidade"
              }
              onClick={() => this._handleFinalidadeChange("Alugar")}
            >
              <span>{imovelProp[lang].alugar}</span>
            </div>
          </div>
          <div className="buscaForm__tipo buscaForm__box">
            <h3>
              <FontAwesomeIcon icon={["fas", "home"]} />
              {imovelProp[lang].tipoDoImovel}
            </h3>
            <div className="buscaForm__box_options">
              {["Casa", "Apartamento", "Cobertura", "Comercial"].map(item => {
                return (
                  <div className="form-check">
                    <input
                      className="form-check-input"
                      type="checkbox"
                      value=""
                      id={item}
                    />
                    <span className="checkmark" />
                    <label className="form-check-label" for={item}>
                      {imovelProp[lang][item]}
                    </label>
                  </div>
                );
              })}
            </div>
          </div>
          <div className="buscaForm__bairro buscaForm__box">
            <FontAwesomeIcon
              icon={
                this.state.showBairros
                  ? ["fas", "chevron-up"]
                  : ["fas", "chevron-down"]
              }
              onClick={this._handleShowBairros}
            />
            <h3 onClick={this._handleShowBairros}>
              <FontAwesomeIcon icon={["fas", "map-marker-alt"]} />
              {imovelProp[lang].bairro}
            </h3>
            <div
              className={
                this.state.showBairros
                  ? "buscaForm__bairro_box buscaForm__box_options"
                  : "buscaForm__bairro_box buscaForm__box_options hidden"
              }
            >
              {topBairros.map(item => {
                return (
                  <div className="form-check">
                    <input
                      className="form-check-input"
                      type="checkbox"
                      value=""
                      id={item.name}
                    />
                    <span className="checkmark" />
                    <label className="form-check-label" for={item.name}>
                      {item.name}
                    </label>
                  </div>
                );
              })}
              <div className="form-check">
                <input
                  className="form-check-input"
                  type="checkbox"
                  value=""
                  id="outros"
                />
                <span className="checkmark" />
                <label className="form-check-label" for="outros">
                  Outros
                </label>
              </div>
            </div>
          </div>
          <div className="buscaForm__area buscaForm__box">
            <h3>
              <FontAwesomeIcon icon={["fas", "expand"]} />
              {imovelProp[lang].area}
            </h3>
            <input
              type="text"
              className="minArea"
              value={this.state.area.min}
            />
            <InputRange
              maxValue={100}
              minValue={0}
              value={this.state.area}
              formatLabel={area => `${area}m¬≤`}
              onChange={area => this.setState({ area })}
              onChangeComplete={area => console.log(area)}
            />
            <input
              type="text"
              className="maxArea"
              value={this.state.area.max}
            />
          </div>
          <div className="buscaForm__preco buscaForm__box">
            <h3>
              <FontAwesomeIcon icon={["fas", "dollar-sign"]} />
              {imovelProp[lang].preco}
            </h3>
            <input
              type="text"
              className="minArea"
              value={this.state.preco.min}
            />
            <InputRange
              maxValue={100}
              minValue={0}
              value={this.state.preco}
              formatLabel={preco => `R$${preco}`}
              onChange={preco => this.setState({ preco })}
            />
            <input
              type="text"
              className="maxArea"
              value={this.state.preco.max}
            />
          </div>
          <div className="buscaForm__quartos buscaForm__box">
            <h3>
              <FontAwesomeIcon icon={["fas", "bed"]} />
              {imovelProp[lang].quartos}
            </h3>
            {[1, 2, 3, 4, "5+"].map(item => {
              return (
                <div className="form-check">
                  <input
                    className="form-check-input"
                    type="checkbox"
                    value=""
                    id={`quarto${item}`}
                  />
                  <label className="form-check-label" for={`quarto${item}`}>
                    {item}
                  </label>
                </div>
              );
            })}
          </div>
          <div className="buscaForm__vagas buscaForm__box">
            <h3>
              <FontAwesomeIcon icon={["fas", "car"]} />
              {imovelProp[lang].vagas}
            </h3>
            {[1, 2, 3, 4, "5+"].map(item => {
              return (
                <div className="form-check">
                  <input
                    className="form-check-input"
                    type="checkbox"
                    value=""
                    id={`vaga${item}`}
                  />
                  <label className="form-check-label" for={`vaga${item}`}>
                    {item}
                  </label>
                </div>
              );
            })}
          </div>
        </div>
      </StickyBox>
    );
  }
}

export default BuscaFormulario;
